:root {
  --brand-gold: #ddb70d;
  --brand-gold-translucent: #ddb70dc9;
  --hover-effect-color: rgba(179, 23, 23, 0.781);
  --button-border-radius: 0.6em;
  --font-primary: 'Montserrat', sans-serif;
  --font-size-button: 17px;
}

.app-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  position: relative; /* Necessário para posicionar os círculos */
  /* Adicionando o gradiente diretamente ao container principal */
  background-color: #1b263b; /* Cor de fallback */
  background-image: linear-gradient(160deg, #1b263b 0%, #5c0017 100%);
  overflow: hidden; /* Garante que os círculos não ultrapassem a tela */
}

/* Círculo no canto superior esquerdo */
.app-container::before {
  content: '';
  position: absolute;
  width: 400px;
  height: 400px;
  background-color: rgba(221, 183, 13, 0.05); /* Usando a cor base de --brand-gold */
  border-radius: 50%;
  top: -150px;
  left: -150px;
  z-index: 0; /* Coloca o círculo atrás do conteúdo */
}

/* Círculo no canto inferior direito */
.app-container::after {
  content: '';
  position: absolute;
  width: 500px;
  height: 500px;
  background-color: rgba(221, 183, 13, 0.04); /* Usando a cor base de --brand-gold */
  border-radius: 50%;
  bottom: -200px;
  right: -200px;
  z-index: 0; /* Coloca o círculo atrás do conteúdo */
}

/* Wrapper para o conteúdo principal para garantir que fique na frente */
.content-wrapper {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.custom-btn {
  font-size: var(--font-size-button);
  font-family: var(--font-primary);
  padding: 1em 2.7em;
  font-weight: 500;
  background: var(--brand-gold);
  color: white;
  border: none;
  position: relative;
  overflow: hidden;
  border-radius: var(--button-border-radius);
  cursor: pointer;
}

.gradient {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  border-radius: var(--button-border-radius);
  margin-top: -0.25em;
  background-image: linear-gradient(
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.3)
  );
}

.label {
  position: relative;
  top: -1px;
}

.transition {
  transition-timing-function: cubic-bezier(0, 0, 0.2, 1);
  transition-duration: 500ms;
  background-color: var(--hover-effect-color);
  border-radius: 9999px;
  width: 0;
  height: 0;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.custom-btn:hover .transition {
  width: 14em;
  height: 14em;
}

.custom-btn:active {
  transform: scale(0.97);
}
.login-link {
  margin-top: 25px; /* Espaçamento acima do texto */
  font-family: var(--font-primary);
  font-size: 16px;
  color: #ffffff; /* Cor para o texto "Já possui um cadastro?" */
}

.login-link a {
  color: var(--brand-gold); /* Cor do link usando a variável da marca */
  font-weight: 600; /* Deixa o link em negrito para dar destaque */
  text-decoration: none; /* Remove o sublinhado padrão */
  transition: text-decoration 0.2s ease-in-out;
}

.login-link a:hover {
  text-decoration: underline; /* Adiciona sublinhado ao passar o mouse */
}

/* --- ESTILOS DO MENU --- */

/* Container para posicionar o menu e o ícone no canto */
.menu-container {
  position: absolute;
  top: 30px;
  left: 30px;
  z-index: 10; /* Garante que o menu fique acima de outros elementos */
}

/* Esconde o checkbox */
.label-check {
  display: none;
}

/* Área clicável do ícone */
.hamburger-label {
  width: 70px;
  height: 58px;
  display: block;
  cursor: pointer;
  position: relative; /* Essencial para a animação das barras */
  z-index: 11; /* Garante que o ícone fique ACIMA do painel do menu */
}

.hamburger-label div {
  width: 70px;
  height: 6px;
  background-color: #ffffff;
  position: absolute;
}

.line1 {
  transition: all .3s;
}

.line2 {
  margin: 18px 0 0 0;
  transition: 0.3s;
}

.line3 {
  margin: 36px 0 0 0;
  transition: 0.3s;
}

/* Animação do ícone quando o menu está aberto */
.menu-open .line1 {
  transform: rotate(35deg) scaleX(.55) translate(39px, -4.5px);
  border-radius: 50px 50px 50px 0;
}

.menu-open .line3 {
  transform: rotate(-35deg) scaleX(.55) translate(39px, 4.5px);
  border-radius: 0 50px 50px 50px;
}
.menu-open .line2 {
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  width: 45px;
}

/* Estilização do Menu Lateral Expansível */
.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 0; /* Começa fechado */
  height: 100vh;
  background-color: var(--brand-gold-translucent);
  overflow: hidden; /* Esconde o conteúdo quando o menu está fechado */
  transition: width 0.4s cubic-bezier(0.86, 0, 0.07, 1);
  z-index: 9; /* Fica atrás do ícone do hamburguer mas na frente do resto */
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  text-align: center;
}

.menu li {
  opacity: 0; /* Itens ficam invisíveis quando o menu está fechado */
  transition: opacity 0.3s ease-in-out 0.2s;
}

.menu a {
  display: block;
  padding: 20px;
  font-family: var(--font-primary);
  font-size: 22px;
  color: rgb(255, 255, 255);
  text-decoration: none;
  font-weight: 600;
  transition: background-color 0.3s;
}

.menu a:hover {
  background-color: var(--hover-effect-color);
}

/* Expande o menu e mostra os itens quando o checkbox está marcado */
.menu-open .menu {
  width: 300px; /* Largura do menu quando aberto */
}

.menu-open .menu li {
  opacity: 1;
}
